.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio, .dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio, .dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-mode table.propiedades td {
  color: #FFFFFF;
  border-bottom: 1px solid #626262;
}

.header {
  background-color: #ffffff;
  padding: 0 0 3rem 0;
}
.header img {
  height: 8rem;
}
.header .contenedorLogo {
  width: 16rem;
  height: 8rem;
}
.header .oscurecericono {
  filter: invert(100%);
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.6;
  max-width: 60rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    text-align: left;
  }
}

.footer {
  background-color: #ffffff;
  padding: 0.5rem;
  height: auto;
}
@media (min-width: 480px) {
  .footer {
    position: fixed;
    left: 0px;
    bottom: 0px;
    width: 100%;
  }
}
.footer .copyright {
  font-size: 1.5rem;
  text-align: center;
  padding-top: 2rem;
}
.footer .copyright a {
  color: rgb(45, 45, 45);
}

.footerRegistros {
  background-color: #ffffff;
  padding: 1rem;
  height: auto;
}
.footerRegistros .copyright {
  font-size: 1.5rem;
  text-align: center;
  padding-top: 2rem;
}
.footerRegistros .copyright a {
  color: rgb(45, 45, 45);
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  transition: visibility 0.1s ease-in-out, opacity 0.1s linear;
  opacity: 0;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #000000;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  margin: 1.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}
.navegacion a:last-of-type {
  margin-right: 0;
  color: rgb(134, 20, 20);
}
.navegacion a:hover {
  color: rgb(134, 20, 20);
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  padding-top: 1rem;
  padding-bottom: 0;
  margin-bottom: 0;
  width: 5rem;
}

